        id='fat'
        required
        v-model="fatGrams"
      />
      <label for="fat" class="form__label">Tłuszcze</label>
    </div>
    <div class="form__group field">
      <input
        type="input"
        class="form__field"
        placeholder="Białko"
        name="protein"
        id='protein'
        required
        v-model="proteinGrams"
      />
      <label for="protein" class="form__label">Białka</label>
    </div>
    <div class="form__group field">
      <input
        type="input"
        class="form__field"
        placeholder="Cukry"
        name="carbs"
        id='carbs'
        required
        v-model="carbGrams"
      />
      <label for="carbs" class="form__label">Cukry</label>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'Home',
  data() {
    return {
      maxKcal: 1200,
      fatGrams: null,
      proteinGrams: null,
      carbGrams: null,
    };
  },
  computed: {
    leftKcal() {
      return this.maxKcal - (this.fatGrams * 9 + 4 * this.proteinGrams + this.carbGrams * 4);
    },
    sumKcal() {
      return this.fatGrams * 9 + 4 * this.proteinGrams + this.carbGrams * 4;
    },
  },
};
</script>

